.section {
  $this: &;
  position: relative;
  background-color: $white;
  padding-top: var(--section-padding-lg);
  padding-bottom: var(--section-padding);
  &__block {
    position: relative;
    left: $left-1-fucked-up;
    width: calc(100% - #{$left-1-fucked-up});
    padding-right: var(--section-padding-md);
    @include lg {
      left: 0;
      width: auto;
      padding-left: var(--section-padding-md); } }
  &__block-left-7-2 {
    left: $left-7-2;
    width: calc(100% - #{$left-7-2});
    @include lg {
      left: 0;
      width: auto;
      padding-left: var(--section-padding-md); } }
  &__block-left-7-4 {
    left: $left-7-4;
    width: calc(100% - #{$left-7-4}); }
  &__title {
    margin-bottom: 35px;
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__word {
    position: absolute;
    z-index: 0; }

  &--gray {
    background-color: $gray-lightest; }
  &--bg {
    @include cover; }
  &--lg {
    padding-bottom: var(--section-padding-lg); }
  &--oil {
    padding-top: 20%;
    padding-bottom: var(--section-padding-lg);
    #{$this}__block-left-7-2 {
      margin-bottom: 85px;
      @include md {
        margin-bottom: 65px; }
      @include sm {
        margin-bottom: 45px; } }
    #{$this}__block-left-7-4 {
      max-width: 585px; } }
  &--red {
    background-color: $red-accent; }
  &--principles {
    #{$this}__word {
      top: -.3em;
      left: 10%; } } }

.section-block {
  position: relative;
  z-index: 1;
  padding-bottom: 25px;
  @include sm {
    padding-bottom: 15px; }
  @include xs {
    padding-bottom: 10px; }
  &__article {
    padding-left: 25px;
    margin-bottom: 80px;
    p {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 50px;
      padding-left: 0;
      p {
        margin-bottom: 15px; } }
    @include xs {
      margin-bottom: 30px;
      p {
        margin-bottom: 10px; } } }
  &__img {
    padding-top: 35%;
    @include cover; }
  &__img-right {
    position: absolute;
    top: 2em;
    right: var(--section-padding-md);
    bottom: 0;
    z-index: 2;
    width: 38%;
    @include cover;
    @include sm {
      display: none; } } }

.three-cols-section {
  background-color: $black;
  padding: 0;
  height: calc(100vh - #{$header-height});
  @include lg {
    height: calc(100vh - #{$header-height-mob}); }
  @include xs {
    height: auto; }
  .section__title {
    position: absolute;
    top: var(--section-padding-lg);
    left: calc(var(--section-padding-lg) * 1.5);
    margin-bottom: 0;
    @include xs {
      top: calc(var(--section-padding-lg) - 15px);
      left: var(--section-padding); } }
  &__cols {
    @include flex();
    height: 100%;
    @include xs {
      display: block; } } }

.three-cols-section-col {
  --three-cols-section-col-padding-left: 40px;
  $this: &;
  padding-top: calc(var(--section-padding-lg) * 2);
  padding-bottom: calc(var(--section-padding-lg) * 2);
  @include flex(center, normal, column);
  overflow: hidden;
  position: relative;
  @include hover {
    @include before {
      opacity: .6; }
    #{$this}__bg {
      @include scale(1.1); } }

  @include before {
    @include coverdiv;
    z-index: 1;
    background-color: #000;
    opacity: .8;
    @include tr(opacity .3s); }
  @include lg {
    --three-cols-section-col-padding-left: 20px; }
  &:nth-child(1) {
    width: $width-1-fucked-up-2;
    padding-left: calc(var(--section-padding-lg) * 1.5);
    @include xs {
      width: 100%;
      padding-left: var(--section-padding);
      padding-top: calc((var(--section-padding-lg) * 2) + 20px); } }
  &:nth-child(2) {
    width: $width-2-fucked-up-2;
    padding-left: var(--three-cols-section-col-padding-left);
    @include xs {
      width: 100%;
      padding-left: var(--section-padding); } }
  &:nth-child(3) {
    width: $width-3-fucked-up-2;
    padding-left: var(--three-cols-section-col-padding-left);
    @include xs {
      width: 100%;
      padding-left: var(--section-padding); } }
  &__bg {
    @include coverdiv;
    @include cover;
    z-index: 0;
    @include tr(transform .3s); }
  &__nmb {
    position: relative;
    margin-bottom: 20px;
    z-index: 3;
    .text-stroke {
      left: -.5em;
      position: relative;
      transform: translateX(-100%); }
    @include before {
      bottom: 0;
      left: 0;
      background-color: $red;
      height: 1px;
      width: 18px;
      position: absolute; } }
  &__text {
    max-width: 280px;
    position: relative;
    z-index: 3;
    opacity: 0; } }
