.item-gallery {
  padding-bottom: 10px;
  &__img {
    display: block;
    @include coverdiv;
    @include cover;
    background-size: contain;
    opacity: 0;
    visibility: hidden;
    @include tr(opacity .3s, visibility .3s);
    &.is-active {
      visibility: visible;
      opacity: 1; } }
  &__preview {
    position: relative;
    padding-top: 70%; }

  &__thumbs {
    padding: 10px;
    background-color: $white;
    // height: 40px
    @include flex(flex-start, center); }
  &__thumb {
    display: block;
    border: 1px solid  $c-border;
    text-align: center;
    &.is-active {
      border-color: var(--main-color); } } }

.drift-zoom-pane {
  left: 10px;
  right: 10px;
  width: auto;
  @include md {
    display: none; } }
